<template>
	<div>
		<Header :title="msg"></Header>
			<div class="data_list" ref="homePage">
				<van-row>
					<van-col :span="6" :offset="1">
						<div v-show="ProvinceBtnShow" @click="toggleProvince"><van-icon name="success" v-show="CityBtnShow"></van-icon>{{ProvinceValue}}</div>
					</van-col>
					<van-col :span="6" :offset="1">
						<div v-show="CityBtnShow" @click="toggleCity"><van-icon name="success"  v-show="AreaBtnShow"></van-icon>{{CityValue}}</div>
					</van-col>
					<van-col :span="6" :offset="1">
						<div v-show="AreaBtnShow" @click="toggleArea"><van-icon name="success" v-show="chooseStatus"></van-icon>{{AreaValue}}</div>
					</van-col>
				</van-row>
				<vue-better-scroll v-show="ProvinceShow" class="wrapper"
				                   ref="scroll1"
																	:startY="parseInt(startY)"
				                  >
				<ul  ref="page">
					<li @click="ProvinceClick($event,index)"  v-for="(item,index) in items" :key="item.id">{{item.province}}</li>
				</ul>
				</vue-better-scroll>
				<vue-better-scroll  v-show="CityShow" class="wrapper"
				                   ref="scroll2"
																	:startY="parseInt(startY)"
				                  >
				<ul  ref="page">
					<li @click="CitiesClick($event,index)" v-for="(item1,index) in items[provinceIndex].children" :key="item1.id">{{item1.city}}</li>
				</ul>
				</vue-better-scroll>
				<vue-better-scroll  v-show="AreaShow" class="wrapper"
				                   ref="scroll3"
																	:startY="parseInt(startY)"
				                  >
				<ul  ref="page">
					<li @click="AreaClick($event,index)" v-for="(item3,index) in items[provinceIndex].children[cityIndex].children">{{item3}}</li>
				</ul>
				</vue-better-scroll>
			</div>
	</div>
</template>

<script>
	import Header from "../../components/header"
	export default{
		data(){
			return {
				msg:"选择城市",
				ProvinceValue:"请选择省…",
				CityValue:"请选择市…",
				AreaValue:"请选择区…",
				locationValue:"",
				clientHeight:"",
				ProvinceShow:true,
				CityShow:false,
				AreaShow:false,
				ProvinceBtnShow:true,
				CityBtnShow:false,
				AreaBtnShow:false,
				provinceIndex:0,
				cityIndex:0,
				areaIndex:0,
				chooseStatus:false,
				startY: 0, // 纵轴方向初始化位置
				scrollToX: 0,
				scrollToY: 0,
				scrollToTime: 700,
				items:[
					{
						"id":1,
						"province":"四川省",
						"children":[
							{
								
								"id":2,
								"city":"成都市",
							"children":["金堂县","青白江区","武侯区"],
							},
							{
								"id":3,	
								"city":"乐山市",
							"children":["金堂县","青白江区","武侯区"],
							},
							{
								"id":4,	
								"city":"宜宾市",
							"children":["金堂县","青白江区","武侯区"],
							}
						]
					},
					{
						"id":5,	
						"province":"广东省",
						"children":[
							{
								"id":6,	
								"city":"深圳市",
							"children":["宝安区","南山区","福田区"],
							},
							{
								"id":7,	
								"city":"广州市",
							"children":["宝安区","南山区","福田区"],
							},
							{
									"id":8,	
								"city":"惠州市",
							"children":["宝安区","南山区","福田区"],
							}
						]
					},
					{
						"id":9,	
						province:"湖南省",
						children:[
							{
								"id":10,		
								"city":"长沙市",
							"children":["宝安区","南山区","福田区"],
							},
							{
								"id":11,			
								"city":"益阳市",
							"children":["宝安区","南山区","福田区","宝安区","南山区","福田区","宝安区","南山区","福田区","宝安区","南山区","福田区","宝安区","南山区","福田区","宝安区","南山区","福田区","宝安区","南山区","福田区","宝安区","南山区","福田区","宝安区","南山区","福田区","宝安区","南山区","福田区","宝安区","南山区","福田区","宝安区","南山区","福田区","宝安区","南山区","福田区","宝安区","南山区","福田区","宝安区","南山区","福田区"],
							}
						]
					}
					
				]
			}
		},
		components:{
			Header,
		},
		methods:{
				toggleProvince(){
					this.ProvinceShow = true;
					this.CityShow = false;
					this.AreaShow = false;
				},
				toggleCity(){
					this.ProvinceShow = false;
					this.CityShow = true;
					this.AreaShow = false;
				},
				toggleArea(){
					this.ProvinceShow = false;
					this.CityShow = false;
					this.AreaShow = true;
				},
				ProvinceClick(e,index){
					this.ProvinceValue = e.currentTarget.innerText;
					this.ProvinceShow = false;
					this.ProvinceBtnShow = true;
					this.CityBtnShow = true;
					this.CityShow = true;
					this.provinceIndex = index;
					// console.log(this.provinceIndex);
					this.chooseStatus = false;
				},
				CitiesClick(e,index){
					this.CityValue = e.currentTarget.innerText;
					this.AreaShow = true;
					this.AreaBtnShow = true;
					this.CityShow = false;
					this.cityIndex = index;
					// console.log(this.cityIndex);
				},
				AreaClick(e,index){
					this.AreaValue = e.currentTarget.innerText;
					this.CityShow = false;
					this.areaIndex = index;
					this.chooseStatus = true;
					// console.log(this.cityIndex);
					this.locationValue = this.ProvinceValue+this.CityValue+this.AreaValue;
					sessionStorage.setItem("location",JSON.stringify(this.locationValue));
					if(this.locationValue!==""){
						this.$router.push("/store");
					}
				},
				scrollTo() {
							this.$refs.scroll1.scrollTo(this.scrollToX, this.scrollToY, this.scrollToTime)
							this.$refs.scroll2.scrollTo(this.scrollToX, this.scrollToY, this.scrollToTime)
							this.$refs.scroll3.scrollTo(this.scrollToX, this.scrollToY, this.scrollToTime)
						}
		},
		mounted(){
			// this.$axios.post()
			
			
			
			
			
			this.clientHeight = document.body.offsetHeight;
			console.log(this.clientHeight);
			if(window.devicePixelRatio == 1){
				this.$refs.homePage.style.height = this.clientHeight - 80+ "px";
			console.log(this.clientHeight);
			}else if(window.devicePixelRatio == 2){
				this.$refs.homePage.style.height = this.clientHeight - 80*2+ "px";
				console.log(this.clientHeight);
			}else if(window.devicePixelRatio == 3){
				this.$refs.homePage.style.height = this.clientHeight - 80*3+ "px";
				console.log(this.clientHeight);
			}
		},
		created(){
			var formData = new FormData();
			formData.append("store_id",1);
			console.log(formData);
			let config = {
				headers: {"Content-Type":"application/json"}
			}
			this.$axios.post("/a.php",formData,config).then((data)=>{
			console.log(this.items);
			console.log(data);
			// /v3/area_list
			this.items = data.data;
				
				
				
				
				
				
				
				
			// var formData = new FormData();
			// formData.append("store_id",1);
			// console.log(formData);
			// let config = {
			// 	headers: {"Content-Type":"application/json"}
			// }
			// this.$axios.post("/v3/area_list",formData,config).then((data)=>{
			// console.log(this.items);
			// console.log(data);
			// // /v3/area_list
			// this.items = data.data;
			})
		}
	}
</script>

<style scoped lang="stylus">
	div.data_list
		width: totalWid
		height: 500px
		background:backColor
		ul
			background:cwhite
			width: totalWid
			height: auto
			background:cwhite
			li
				width: totalWid
				height: 88px
				margin-left: 30px
				line-height: 88px
				border-bottom:1PX solid cgraye5
				color:cblack
				font-size:17PX
				box-sizing:border-box
				font-weight:normal
			li:first-child
				border-top:1PX solid cgraye5 
		.van-row
			background:cwhite
			height: 88px
			.van-col
				height: 88px
				div
					width: 168px
					height: 60px
					line-height: 60px
					margin:14px 0
					border:1PX solid cblue
					box-sizing:border-box
					text-align:center
					color:cblue
					font-size:14PX
					ellipsis()
	[data-dpr="2"] div.data_list
		ul
			li
				font-size:font34
		.van-row
			.van-col
				div
					font-size:font28
		/div.next_btn
			& .van-button--large
				font-size:font34
	[data-dpr="3"] div.data_list
		ul
			li
				font-size:font51
		.van-row
			.van-col
				div
					font-size:font42
</style>



