// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import 'lib-flexible'
import axios from 'axios'
import VueAxios from 'vue-axios'
import cors from 'cors'
import qs from 'qs'
import Vuex from 'vuex'
import './assets/reset.css'
import'./assets/css/initInput.css'
import './assets/style.styl'
import Vant from 'vant'
import 'vant/lib/index.css'
import BaiduMap from 'vue-baidu-map'
import AlloyFinger from 'alloyfinger'
import AlloyFingerPlugin from 'alloyfinger/vue/alloy_finger_vue'
import VueBetterScroll from 'vue2-better-scroll'
import VueJsonp from "vue-jsonp"
axios.defaults.withCredentials = false
// axios.defaults.timeout = 5000
// axios.defaults.baseURL = 'http://127.0.0.1:80'
axios.defaults.baseURL = 'http://192.168.5.15'
// axios.defaults.baseURL = 'http://47.111.27.189:2000'
// axios.defaults.headers.common['Authorization'] = AUTH_TOKEN
axios.defaults.headers = {'Content-Type':'application/x-www-form-urlencoded'}//对象方式传递
// axios.defaults.headers = {'Content-Type':'application/text/plain'}
Vue.config.productionTip = false
/* eslint-disable no-new */
Vue.use(Vuex)
Vue.use(Vant)
Vue.use(VueJsonp)
Vue.use(BaiduMap, {
  // ak 是在百度地图开发者平台申请的密钥 详见 http://lbsyun.baidu.com/apiconsole/key */
  ak: 'efhNFs0eQd5NA9cLUnNeIt4XwK6xvBVW'
})
// Vue.use(cors({
// 	origin:["http://127.0.0.1:8080"],
// 	methods:["GET","POST"],
// 	allowHeaders:["Content-Type","Authoriation"]
// 	}
// ))
Vue.use(AlloyFingerPlugin,{
  AlloyFinger
})
Vue.use(VueBetterScroll)
Vue.prototype.$axios = axios
Vue.prototype.qs = qs
// Vue.prototype.HOST = '/api'
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})


