<template>
	<div class="chooselocation" ref="homePage">
		<Header :title="msg"></Header>
		<van-cell-group class="input_lo input_center">
				 <van-field left-icon="search" v-model="keyword" placeholder="请输入您的店铺地址"/>
		</van-cell-group>
		<div class="map_container" ref="map">	
			<baidu-map class="map" :ak="ak" :center="center" :zoom="zoom" @ready="handler" @touchend="getPoint($event)" @load="loadding"  ref="bdmap">
<bm-geolocation anchor="BMAP_ANCHOR_BOTTOM_RIGHT" :showAddressBar="true" :autoLocation="true"></bm-geolocation>
    <bm-marker :position="{lng:center.lng, lat:center.lat}" :dragging="true" animation="BMAP_ANIMATION_BOUNCE" :icon="{url:'/static/img/x2/icon_lc_xhao@2x.png',size:{width:20,height:20}}">
    </bm-marker>
	<bm-local-search :keyword="keyword" :auto-viewport="true" :location="location"></bm-local-search>
  </baidu-map>
		</div>
		<div class="van-picker">
			<vue-better-scroll class="wrapper" ref="scroll" :startY="parseInt(startY)">
			<van-row class="column" v-for="(column,index) in columns">
				<van-col :span="22">
					<div @touchend="chooseClick(index)">
						<p>{{column.bTitle}}</p>
						<p>{{column.lTitle}}</p>
					</div>
				</van-col>
				<van-col :span="2">
					<van-icon name="success" :class="{active:index== current}"></van-icon>
				</van-col>
			</van-row>
			<van-row class="column">
				<van-col> </van-col>
			</van-row>
			</vue-better-scroll>
		</div>
		<van-button size="large" square type="info" @click="next">确认地址</van-button>
	</div>
</template>

<script>
	import Header from "../header"
	import BaiduMap from "vue-baidu-map/components/map/Map.vue"
	
	export default{
		data(){
			return {
				msg:"选择位置",
				ak:"efhNFs0eQd5NA9cLUnNeIt4XwK6xvBVW",
				center: { lng:114.02597366, lat:22.54605355},//当前经纬度
				zoom: 16,//地图显示大小
				location: "",//location
				keyword: "",//搜索关键词
				address:"",//地图中的选中地址
				current:0,//选中的index
				confirmedAddress:"",
				jgNameDialog: false,
				show: false,
				startY: 0, // 纵轴方向初始化位置
				scrollToX: 0,
				scrollToY: 0,
				scrollToTime: 700,
				locationList:[],
				geolocation:null,
				map:null,
				add:{
					province:"",
					city:"",
					district:"",
					street:"",
					streetNumber:""
				  },
				columns:[
					{
						"id":1,
						"bTitle":"海关大厦",
						"lTitle":"新安路28号"
					},
					{
						"id":2,
						"bTitle":"杭州",
						"lTitle":"杭州市后巷2"
					},
					{
						"id":3,
						"bTitle":"杭州",
						"lTitle":"杭州市后巷3"
					},{
						"id":4,
						"bTitle":"杭州",
						"lTitle":"杭州市后巷4"
					},{
						"id":5,
						"bTitle":"杭州",
						"lTitle":"杭州市后巷5"
					},
					{
						"id":6,
						"bTitle":"杭州",
						"lTitle":"杭州市后巷5"
					},
					{
						"id":7,
						"bTitle":"杭州",
						"lTitle":"杭州市后巷5"
					},
					{
						"id":8,
						"bTitle":"杭州",
						"lTitle":"杭州市后巷5"
					},
					{
						"id":9,
						"bTitle":"杭州",
						"lTitle":"杭州市后巷5"
					},
					{
						"id":10,
						"bTitle":"杭州",
						"lTitle":"杭州市后巷5"
					}
				],
				initLocation: false,
			}
		},
		components:{
			Header,
			BaiduMap,
		},
		methods:{
			handler ({BMap, map}) {
				let _this = this;  
				_this.geolocation = new BMap.Geolocation();
				_this.geolocation.getCurrentPosition(function(r){
					if(this.getStatus() == BMAP_STATUS_SUCCESS){
						var mk = new BMap.Marker(r.point);
						map.addOverlay(mk);
						map.panTo(r.point);
						_this.center.lng = r.point.lng;
						_this.center.lat = r.point.lat;
						theLocation(r.point.lng,r.point.lat);
						function theLocation(lng,lat){
							map.clearOverlays(); 
							var new_point = new BMap.Point(lng,lat);
							var marker = new BMap.Marker(new_point);  // 创建标注
							map.addOverlay(marker);              // 将标注添加到地图中
							map.panTo(new_point);      
						}

						console.log(_this.center.lng,_this.center.lat);
	 
						_this.$jsonp("http://api.map.baidu.com/geocoder/v2/?ak=efhNFs0eQd5NA9cLUnNeIt4XwK6xvBVW&location="+_this.center.lat+","+_this.center.lng+"&output=json&pois=1")
						.then((data)=>{
							_this.locationList = data;
							// console.log(_this.locationList);
							console.log(_this.locationList.result.pois[0].name);
							for(var i in _this.locationList.result.pois){
								// console.log(i);
								_this.columns[i].bTitle = _this.locationList.result.pois[i].name;
								_this.columns[i].lTitle = _this.locationList.result.pois[i].addr;
							}
							
						})
					}else {
						// alert('failed'+this.getStatus());
						this.$toast("加载失败，请重新进入页面");
						}        
					});   
				},
			loadding(){
					console.log(this.center);
					
					
				},
			chooseClick(index){
				this.current = index;
				console.log(this.current);
				this.confirmedAddress = this.columns[this.current].lTitle;
				sessionStorage.setItem("detailLocation",JSON.stringify(this.confirmedAddress));
			},
			hand(){
				this.$toast({
					message:"hello",
					});
			},
			getPoint(e){
			// 	this.center.lng = e.point.lng;     //通过  e.point.lng获取经度
			// 	this.center.lat = e.point.lat;   
			// 	console.log(this.center);
			// let geocoder = new BMap.Geocoder();//创建地址解析器
			// geocoder.getLocation(e.point,rs=>{
			// 	this.address = rs.address;
			// 	console.log(rs.address);//广东省深圳市宝安区
			// 	console.log(rs.addressComponents);//address的json结构化描述
			// })//地址
					
	
	
	
	
	
	
	
	
			},
			scrollTo() {
							this.$refs.scroll.scrollTo(this.scrollToX, this.scrollToY, this.scrollToTime)
			},
			next(){
				this.$router.push("/store");
			}
		},
		mounted(){
			console.log(document.body.offsetHeight);
			this.$refs.homePage.style.minHeight = document.body.offsetHeight + "px";
			this.$refs.map.style.height = document.body.offsetHeight - 88*3 + "px";
			// http://api.map.baidu.com/geocoder/v2/?ak=efhNFs0eQd5NA9cLUnNeIt4XwK6xvBVW&location=22.54605355,114.02597366&output=json&pois=1
			
			
		},
		watch:{
			center(){
				
				
				
				
			}
		}

		}
</script>

<style scoped lang="stylus">
	div.chooselocation
		width:totalWid
		height: auto
		.input_lo
			position:absolute
			z-index:100
			top: 122px
			left: 30px
			width: totalWid - 60px
			height: 80px
			line-height: 80px
		.van-picker
			position:absolute
			z-index:100
			bottom: 0
			left: 0;
			width: totalWid + 16px
			height: 650px;
			background:cwhite
			overflow:hidden
		.van-row
			border-bottom:informationBorder
			margin-left: 15px
			.van-col
				height: 130px
				div
					p
						padding:10px 0
					p:first-child
						font-size:17PX
						color:cblack
					p:last-child
						font-size:15PX
						color:cgray9
						ellipsis()
						width: 100%
						height: 40px
						
				.van-icon
					line-height: 130px
					display:none
					font-size:17PX
					color:cblue
				.van-icon.active
					display:block
		.map_container
			width: totalWid
			.map
				width: totalWid
				height: 100%
		.van-button
			position:absolute
			z-index:102
			bottom: 0
			left: 0
			height: 88px;
	[data-dpr="2"] div.chooselocation
		.van-row
			.van-col
				div
					p
					p:first-child
						font-size:font34
						color:cblack
					p:last-child
						font-size:font30
						color:cgray9
				.van-icon
					font-size:font34
	[data-dpr="3"] div.chooselocation
		.van-row
			.van-col
				div
					p
					p:first-child
						font-size:font51
						color:cblack
					p:last-child
						font-size:font45
						color:cgray9
				.van-icon
					font-size:font51
</style>



