<template>
	<div class="classify" ref="homePage">
		<Header :title="msg"></Header>
		<div class="classify_level" ref="page">
			<van-row>
				<van-col :span="7">
					<vue-better-scroll
			            ref="scroll1"
						:startY="parseInt(startY)">
						<ul class="classify_level1_ul">
							<li :class="{active_level1:index== current1}" v-for="(listArr,index) in list" 
							 :key="listArr.id" v-if="index<listLength" @touchend="clickLevel1(index,listArr)">
							 <span>
								 {{listArr.name}}
							 </span>
							</li>
						</ul>
					</vue-better-scroll>
				</van-col>
				<van-col :span="8">
					<vue-better-scroll
					    ref="scroll2"
						:startY="parseInt(startY)">
						<ul  class="classify_level2_ul">
								<li  :class="{active_level2:index2 == current2}" v-for="(listArr2,index2) in list[current1].parts" @touchend="clickLevel2(index2,listArr2)">
									{{listArr2.name}}
								</li>
						</ul>
				</vue-better-scroll>
				</van-col>
				<van-col :span="9">
					<vue-better-scroll
					    ref="scroll2"
						:startY="parseInt(startY)">
					<ul  class="classify_level3_ul">
						<li :class="{active_level3:index3 == current3}" v-for="(listArr3,index3) in list[current1].parts[current2].parts" @touchend="clickLevel3(index3,listArr3)">{{listArr3.name}}</li>
					</ul>
				</vue-better-scroll>
				</van-col>
			</van-row>
		</div>
		<van-row class="next_btn" ref="btn">
			<van-col :span="22" :offset="1">
				<router-link to="/checks"><van-button type="info" size="large">提交</van-button></router-link>
			</van-col>
		</van-row>
	</div>
</template>

