				this.current1 = index;
				this.current2 = 0;
				this.current3 = 0;
				this.value1 = el.name;
				// console.log(this.value1);
			},
			clickLevel2(index,el){
				this.current2 = index;
				this.current3 = 0;
				this.value2 = el.name;
				// console.log(this.value2);
			},
