							font-size:font16
	[data-dpr="2"] div.classify
		div.classify_level
			.van-row
				.van-col
					font-size:font34
					ul.classify_level1_ul
						li
							font-size:font34
					ul.classify_level2_ul
						li
							font-size:font34
					ul.classify_level3_ul
						li
							font-size:font34
	[data-dpr="3"] div.classify
	div.classify_level
		.van-row
			.van-col
				font-size:font50
				ul.classify_level1_ul
					li
						font-size:font50
				ul.classify_level2_ul
					li
						font-size:font50
				ul.classify_level3_ul
					li
						font-size:font50
</style>



