<template>
	<div class="application">
		<Header :title="msg"></Header>
		<van-row class="pay_info">
			<van-col :span="1" :offset="4">
				<van-icon name="checked"></van-icon>
			</van-col>
			<van-col :span="16"  :offset="2">
				您的审核已通过，请缴费
			</van-col>
		</van-row>
		<van-row class="count">
			<van-col :span="22" :offset="1">
				<div>
					<p>￥1000</p>
					<p>应缴纳总金额(元)</p>
				</div>
			</van-col>
		</van-row>
		<van-row class="count_detail">
			<van-col :span="22" :offset="1">
				普通店铺一年服务费：900 元+经营保证金：100 元
			</van-col>
		</van-row>
		<van-row class="transfer_voucher">
			<van-col :span="22" :offset="1">
				您可通过以下方式缴费：
			</van-col>
		</van-row>
		<van-row class="voucher">
			<van-col :span="10" :offset="1">
				上传转账凭证<i>*</i>
			</van-col>
			<van-col :span="4" :offset="8">
				<van-uploader :after-read="onRead">
				  <van-icon name="plus" />
				</van-uploader>
			</van-col>
		</van-row>
		<van-row class="transfer_info">
			<van-col :span="22" :offset="1">
				<p>1.请转账至官方提供的收款银行账户</p>         
				<p>2.转账后请上传转账凭证，以便我们及时确认收款</p>
			</van-col>
		</van-row>
		<van-row class="btn">
			<van-col :span="22" :offset="1">
				<router-link to=""><van-button size="large" type="info">提交</van-button></router-link>
			</van-col>
		</van-row>
		
		
	</div>
</template>

<script>
	import Header from "../header"
	export default{
		data(){
			return {
				msg:"入驻申请"
			}
		},
		components:{
			Header
		},
		methods:{
			onRead(){
				
			}
		}
	}
</script>

<style scoped lang="stylus">
	.pay_info
		font-family:font
		color:cblack
		height: 110px
		background:backColor
		.van-icon
			color:cgreen
			font-size:30PX
			height: 110px
			line-height: 110px
		.van-col--16
			height: 110px
			line-height: 120px
			font-size:17PX
	.count
		width: 690px
		height: 222px
		background:cwhite
		.van-col
			width: 690px
			height: 160px
			background:cblue
			margin-top: 30px
			border-radius:8px
			div
				width: 690px
				height: 160px
			p:first-child
				font-size:26PX
				text-align:center
				color:cwhite
				margin-top: 14px
			p:last-child
				height: 30px
				font-size:14PX
				text-align:center
				color:cwhite
				margin-top:15px
				line-height: 30px
	.count_detail
		.van-col
			font-size:14PX
			color:cgray6
			text-align:left
	.transfer_voucher
		color:cblack
		font-size:16PX
		margin-top: 60px
		margin-bottom: 21px
	.voucher
		border-top:informationBorder
		border-bottom:informationBorder
		height: 176px
		.van-col--10
			height: 176px
			line-height: 176px
			font-size:17PX
			i
				color:cred
		.van-col--4
			height: 176px
			.van-uploader
				width: 116px
				height: 116px
				margin:(170px/2)-(120px/2)  0
				text-align:center
				border:normalBorder
				i
					line-height: 120px
					font-size:25PX
					color:#F0F0F0
	.transfer_info
		margin-top: 29px
		font-size:13PX
		color:cgray9
		p:last-child
			margin-top: 10px
	.btn
		margin-top: 58px
		
[data-dpr="1"]
	& .pay_info
			.van-icon
				font-size:font30
			.van-col--16
				font-size:font17
		& .count
			.van-col
				div
				p:first-child
					font-size:font26
				p:last-child
					font-size:font14
		& .count_detail
			.van-col
				font-size:font14
		& .transfer_voucher
			font-size:font16
		& .voucher
			.van-col--10
				font-size:font17
			.van-col--4
				.van-uploader
					i
						font-size:font25
		& .transfer_info
			font-size:font13	
[data-dpr="2"]
	& .pay_info
		.van-icon
			font-size:font60
		.van-col--16
			font-size:font34
	& .count
		.van-col
			div
			p:first-child
				font-size:font52
			p:last-child
				font-size:font28
	& .count_detail
		.van-col
			font-size:font28
	& .transfer_voucher
		font-size:font32
	& .voucher
		.van-col--10
			font-size:font34
		.van-col--4
			.van-uploader
				i
					font-size:font50
	.transfer_info
		font-size:font26
/[data-dpr="3"]
	& .pay_info
		.van-icon
			font-size:font90
		.van-col--16
			font-size:font51
	& .count
		.van-col
			div
			p:first-child
				font-size:font78
			p:last-child
				font-size:font42
	& .count_detail
		.van-col
			font-size:font42
	& .transfer_voucher
		font-size:font48
	& .voucher
		.van-col--10
			font-size:font51
		.van-col--4
			.van-uploader
				i
					font-size:font75
	& .transfer_info
		font-size:font39	
</style>



