		},
		components:{
			Header,
			
		},
		methods:{
			showDialog1(){
				this.$dialog.alert({
				  title: '门脸照示意图',
				  message: `<img src="/static/img/x2/icon_bnt_dpzp@2x.png"><p>门店照需拍出完整门匾，门框，需在店铺开门营业状态下完成拍摄</p>`,
				  confirmButtonText:"知道了"
				}).then(() => {
				  // on close
				})
			},
			showDialog2(){
				this.$dialog.alert({
				  title: '店内照示意图',
				  message: `<img src="/static/img/x2/icon_bnt_dnzp@2x.png"><p>店内照片需要实际反映内部情况（比如收银台、餐桌椅等）</p>`,
				  confirmButtonText:"知道了"
				}).then(() => {
				  // on close
				})
			},
			showDialog3(){
