				  title: 'LOGO示意图',
				  message: `<img src="/static/img/x2/shilitu_bnt_klogo@2x.png"><p>店铺LOGO（支持jpg、jprg、png格式，大小不超过1mb）</p>`,
				  confirmButtonText:"知道了"
				}).then(() => {
				  
				})
			},
			onRead(file) {
				console.log(file);
				this.file = file;
				var formData = new FormData();
				console.log(this.file.file);
				console.log(this.$refs.storeFront);
				console.log(this.$refs.storeFront.$el.childNodes[1]);
				console.log(this.$refs.storeFront.$el.childNodes[1].files);
				formData.append("file",this.file.file);
				console.log(formData);
				let config = {
			 		headers: {"Content-Type":"multipart/form-data"}
			 }
				this.$axios.post("/api/public/v3/image_upload",formData,config).then((data)=>{
			 	console.log(data);
				alert("a");
			 })
				
			// 				this.file = file;
			// 				var formData = new FormData();
			// 				console.log(this.file.file);
			// 				console.log(this.$refs.storeFront);
			// 				console.log(this.$refs.storeFront.$el.childNodes[1]);
			// 				console.log(this.$refs.storeFront.$el.childNodes[1].files);
			// 				formData.append("file",this.file.file);
			// 				console.log(formData);
			// 				let config = {
			// 					headers: {"Content-Type":"multipart/form-data"}
			// 				}
			// this.$axios.post("/v3/image_upload",formData).then((data)=>{
			// 	console.log(data);
			// 	console.log(data);
			// 				alert("a");
			// })
			
			
			
			
				
				},
				choosecity(){
					console.log(this.storeName);
					this.$router.push('/choosecity')
				},
				chooseLocationClick(){
					this.$router.push('/chooselo')
				},
			next(){
				let checkStoreName = /^[\u4e00-\u9fa5A-Za-z0-9-_]{1,15}$/;
				let checkcontactName = /^[\u4e00-\u9fa5A-Za-z0-9-_]{1,15}$/;
				let checkPhone = /\d{11}/;
				this.regExpAll = false;
				// regexp storeName
				if(!checkStoreName.test(this.storeName)||this.storeName==""){
					this.regExpAll = false;
				}
				// regexp contactName
				if(!checkcontactName.test(this.contactName||this.storeName=="")){
					this.regExpAll = false;
				}
				// regexp contactNumber
				if(!checkPhone.test(this.contactNumber||this.storeName=="")){
					this.regExpAll = false;
				}
				// regexp detailLocation
				if(this.detailLocation==""||this.storeName==""){
					this.regExpAll = false;
				}
				// regexp all information
				if(this.regExpAll === true){
					this.$router.push('/ident');
					console.log(this.regExpAll);
				}else{
					console.log(this.regExpAll);
					this.$toast("提交失败，请检查必填项信息是否填写完整");
				}
				
			}
				
			},
